import { Day } from "../types";
import { loadImage } from "../helper";

import qrCode from './qr-code.png';

import eJsKUTRcYSEFfABxBJrzKKe from './frames/frame_000.jpg';
import fjDvshmAAVOOTRvytkckAkN from './frames/frame_001.jpg';
import dyTQucXIPtHdBvmOnYBiKIY from './frames/frame_002.jpg';
import fyygFoXLlHJQreuoXZuNDhe from './frames/frame_003.jpg';
import vcTESVVbbPgeVyPxyEdnaA0 from './frames/frame_004.jpg';
import eSccGUyFGTpwJpNPfQUBnUL from './frames/frame_005.jpg';
import cbRUgnbjsZINRtRKOheHqOZ from './frames/frame_006.jpg';
import dLZzKoXJhsWJSUYjRVVGaYr from './frames/frame_007.jpg';
import dPnajTNsSFWYIZPtpwSbWnG from './frames/frame_008.jpg';
import fjDVFikujRCGniFDEceJpXr from './frames/frame_009.jpg';
import fAveUxpJufpgbFkDUcnBTxM from './frames/frame_010.jpg';
import dgFdLHRzxPUERrAHpFYetid from './frames/frame_011.jpg';
import eXmyLKdfIxAYBQkllTvKlfe from './frames/frame_012.jpg';
import bFpSacKMPOftzxLaNPuvovp from './frames/frame_013.jpg';
import arAJAomeRVNIPwmesJQJwwp from './frames/frame_014.jpg';

import preval from 'preval.macro';

export default class Day02 extends Day {
  public static __hash = "aiDjqteQNhVBRLiHceCSRIO";
  public static __name = "Day02";
  public static __qrCode = qrCode;

  public static __content = async () => preval`
    const marked = require('marked');
    const twemoji = require('twemoji');
    const path = require('path');
    const fs = require('fs');

    const markdownPath = path.join(__dirname, 'content.md');
    const markdown = fs.readFileSync(markdownPath, 'utf-8')

    const _html = marked(markdown);
    const html = twemoji.parse(_html);

    module.exports = html;
  `;

  public static framesPromises = [
    loadImage(eJsKUTRcYSEFfABxBJrzKKe),
    loadImage(fjDvshmAAVOOTRvytkckAkN),
    loadImage(dyTQucXIPtHdBvmOnYBiKIY),
    loadImage(fyygFoXLlHJQreuoXZuNDhe),
    loadImage(vcTESVVbbPgeVyPxyEdnaA0),
    loadImage(eSccGUyFGTpwJpNPfQUBnUL),
    loadImage(cbRUgnbjsZINRtRKOheHqOZ),
    loadImage(dLZzKoXJhsWJSUYjRVVGaYr),
    loadImage(dPnajTNsSFWYIZPtpwSbWnG),
    loadImage(fjDVFikujRCGniFDEceJpXr),
    loadImage(fAveUxpJufpgbFkDUcnBTxM),
    loadImage(dgFdLHRzxPUERrAHpFYetid),
    loadImage(eXmyLKdfIxAYBQkllTvKlfe),
    loadImage(bFpSacKMPOftzxLaNPuvovp),
    loadImage(arAJAomeRVNIPwmesJQJwwp),
  ];

  private static frames: HTMLImageElement[];
  private static currentFrame: number = 0;

  private static fps = 1000 / 20;
  private static last = 0;

  public static async init() {
    if (Day02.frames === undefined) {
      await Day02.loadFrames();
    }
  }

  private static async loadFrames() {
    Day02.frames = await Promise.all(Day02.framesPromises);
  }

  public static getFrame() {    
    const now = Date.now();
    const elapsed = now - Day02.last;

    if (elapsed >= Day02.fps) {
      Day02.last = now;
      Day02.currentFrame = (Day02.currentFrame + 1) % Day02.frames.length;
    }

    return Day02.frames[Day02.currentFrame];
  }
}
